import { useState } from 'react'
import { axios_req } from '../axios'
import './Filter.css'

const Filter = ({setExers})=>{
    const [search,setSearch]=useState('')
    const [solution,setSolution] = useState(true)
    const set_solution=(e)=>{
        if(e.target.value=='yes'){
            setSolution(true)
        }else{
            setSolution(false)
        }
    }
    const on_change_search = (e)=>{
        setSearch(e.target.value)
    }
    const on_search = (e)=>{
        e.preventDefault()
        console.log(search)
        if(1){
            let data={
                'q':search,
                'start':2000,
                'end':2100,
                'with_solution':solution
            }
            console.log(solution)
            axios_req.post('exers/',data).then((res)=>{
                    setExers(res.data)
                    console.log(res)
            })
        }
    }
    return (
        <form className='filter' onSubmit={on_search}>
            <div className='filter__top'>
                <input onChange={on_change_search} className='filter__top_inp' placeholder='search ...'/>
            </div>
            <div className='filter__bottom'>
                
                <label  htmlFor='solution-select'> whith solution </label>
            <select id='solution-select' onChange={set_solution}>
                <option value='yes'>yes</option>  
                <option value='no'>no</option>  
            </select>
            <label htmlFor='subjetc'> subject  </label>
            <select id='subject-select'>
            <option value='all'>all</option>

                <option value='math'>math</option>
                <option value='analysis'>analysis</option>
                <option value='algebra'>algebra</option>
            </select>
            <label htmlFor='speciality-select'>speciality </label>
            <select id='speciality-select'>
            <option value='all'>all</option>

                <option value='st'>st</option>
                <option value='mi'>mi</option>
                <option value='physics'>physics</option>
            </select>
            </div>
        <button type='submit' className='search__btn'> search</button>
        </form>
    )
}
export default Filter
