import React from 'react'
import Filter from './Filter'
import Exercises from './Exercises'
import './Home.css';
import { axios_req,base as base_url } from '../axios';
import { useEffect,useState } from 'react';

const Home = () => {
    const [exers,setExers]=useState([])
    useEffect(()=>{
        axios_req.get(`exers/`).then((res)=>{
            setExers(res.data)
            console.log(res.data)
        })
    },[])
    return (
        <div className="container">
            <div className="home">
            <Filter setExers={setExers}/>

                <div className="table">
                    <table width="100%" >
                        <tbody>
                            <tr className="tbl-head">
                                <td>Module</td>
                                <td>Name</td>
                                <td>Date</td>
                                <td>Likes</td>
                                <td>Downloads</td>
                            </tr>
                            
                                {exers.map((ex)=>{
                                    return(

                                <tr>
                                <td>{ex.subject_name}</td>
                                <td>{ex.name}</td>
                                <td>{new Date(`${ex.date}`).toDateString()}</td>
                                <td>{ex.likes}</td>
                                <td><a href={ `${base_url}${ex.ex_file} `}>click me! </a></td>
                            </tr>            
                                    )
                                })}

                           
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}

export default Home
